import React from 'react';
import { Container } from 'react-bootstrap';

/*
    Thank You page
    Created by: Ikasuu, Spring 2021
*/

function ThankYou() {
    return (
        <div>
            <Container style={{marginBottom: '20rem', paddingTop: '9.5rem'}}>
                <div style={{display: 'flex', justifyContent: 'center'}}><img style={{marginBottom: '3rem'}}src="https://maplestory.io/api/GMS/222/mob/9010185/render/move" alt="Logo"/></div>
                <h1 style={{textAlign: 'center', fontWeight: '600', fontSize: '3.5rem', marginBottom: '4rem'}}>Thank you for participating in the Grandis Library Beta!</h1>
                <h2 style={{textAlign: 'center'}}>The new site is now live and can be found at</h2>
                <h3 style={{textAlign: 'center'}}><a href="https://grandislibrary.com/" target="_blank" rel="noreferrer noopener">https://grandislibrary.com/</a></h3>
            </Container>
        </div>
    );
}

export default ThankYou;